.modal-background {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(12,0,50,0.2);
  z-index: 10;
  height: 100%;
  width: 100%;
}

.modal-child {
  position: absolute;
  height: 53%;
  width: 415px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(0,0,0,0.95);
  outline: 1px rgb(34, 0, 103) solid;
  z-index: 11;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  border-radius: 1px;
 
  // box-shadow: 0px 1px 4px 1px #777777;
}


.modalTitle {
  font-family: $roboto-condensed;
  font-size: 36px;
  margin-top: 5px;
  margin-bottom: 55px;
  text-align: center;
  width: 70%;
  color: rgba(255,255,255,0.9);
}

.mod_formWrapper {
  // margin-bottom: 40px;
  width: 100%;
  height: 100%;
}

.mod_closeModal {
  height: 22px;
}

.mod_closeModalButton {
  font-family: $roboto-condensed;
  font-weight: 200;
  position: relative;
  cursor: pointer;
  background-color: rgba(0,0,0,0.95);
  height: 18px;
  opacity: 1;
  width: 18px;
  justify-self: end;
  margin-left: auto;
  flex-shrink: 0;
  // background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  color: rgba(255,255,255,0.3);
  display: flex;
  justify-content: flex-end;
  margin-top: 1px;
}

.mod_closeModalButton p {
  font-family: $roboto-condensed;
  font-weight: 200;
  padding-right: 8px;
  padding-top: 7px;
  background: none;
  transform: translateY(-1px);
}

.mod_closeModalButton:hover {
  transform: translateY(0px);
}

.mod_closeModalButton:focus {
  transform: translateY(-1px);
}

.mod_formInnerWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-top: 0px;
  animation-name: modalFade;
  animation-duration: .3s;
}
@keyframes modalFade {
  from {transform: translateX(-50%);opacity: 0;}
  to {transform: translateY(0);opacity: 1;}
}

