.nb_userInput:focus {
  border-bottom: 1px rgb(73,0,218) solid;
  color: #fff;
  transition: border-radius 0.3s, background 0.3s;
  box-shadow: 0 4px 2px -4px rgba(204,169,44, 0.4);
  animation: pulse 2.8s infinite;
}

.nb_passInput:focus {
  border-bottom: 1px rgb(73,0,218) solid;
  color: #fff;
  transition: border-radius 0.3s, background 0.3s;
  box-shadow: 0 4px 2px -4px rgba(204,169,44, 0.4);
  animation: pulse 2.8s infinite;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 4px 2px -4px rgba(204,169,44, 0.4);
  }
  70% {
      -webkit-box-shadow: 0 0 0 3px rgba(204,169,44, 0);
  }
  100% {
      -webkit-box-shadow: 0 0 0 3px rgba(204,169,44, 0);
  }
}
@keyframes pulse {
  0% {
    -moz-box-shadow: 0 4px 2px -4px rgba(204,169,44, 0.4);
    box-shadow: 0 4px 2px -4px rgba(204,169,44, 0.4);
  }
  70% {
      -moz-box-shadow: 0 0 0 3px rgba(204,169,44, 0);
      box-shadow: 0 0 0 3px rgba(204,169,44, 0);
  }
  100% {
      -moz-box-shadow: 0 4px 2px -4px rgba(204,169,44, 0);
      box-shadow: 0 4px 2px -4px rgba(204,169,44, 0);
  }
}

